//这一步是封装组件中的重点，用到了Vue提供的一个公开方法：install。这个方法会在你使用`Vue.use(plugin)`时被调用，这样使得我们的插件注册到了全局，在子组件的任何地方都可以使用。
// /src/package/index.js
import AKUButton from './zsl-button/index.vue'
import * as AKUpublicjs from './public-js/index.js'


console.log("AKUButton2222", AKUButton);

// 批量注册组件
const components = [AKUButton]

const install = function (Vue) {
  components.forEach(component => {
    Vue.component(component.name, component)
  })
}

console.log("AKUpublicjs", AKUpublicjs);

export default {
  install,
  AKUpublicjs // 这个方法以后再使用的时候可以被use调用
}